.guarantees-section {
  background: url("../../Images/guaranteesBg.png");
  padding-top: 100px;
}

.guarantees-title {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 800;
  line-height: 54px;
}

.guarantees-card {
  border-radius: 20px;
  background: var(--primary-text-color);
  box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.08);
  width: 410px;
  height: 160px;
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.guarantees-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 24px;
  grid-row-gap: 24px;
  padding-top: 40px;
}

.guarantees-card-title {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 36px;
  max-width: 251px;
}

.guarantees-agreement {
  display: flex;
  justify-content: space-between;
  padding-top: 32px;
}

.guarantees-agreement h3 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  max-width: 700px;
}

.guarantees-download {
  display: flex;
  align-items: center;
  gap: 10px;
}

.guarantees-download,
.guarantees-download a {
  color: var(--primary-bg-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
}

.advantages-block {
  padding-top: 100px;
  padding-bottom: 100px;
}

.advantages-block-title {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px;
}

.advantage-card h3 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 44px;
}

.advantage-card p {
  color: rgba(0, 0, 0, 0.8);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  padding-top: 20px;
}

.advantage-card {
  border-radius: 20px;
  background: var(--primary-text-color);
  box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.08);
  padding: 40px 44px;
}

.advantages-block-cards {
  display: flex;
  align-items: center;
  gap: 24px;
  padding-top: 40px;
}

@media screen and (max-width: 1300px) {
  .guarantees-cards {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    margin: 0 auto;
  }

  .guarantees-card {
    margin: 0 auto;
  }
}

@media screen and (max-width: 1150px) {
  .guarantees-cards {
    display: flex;
    flex-direction: column;
  }

  .guarantees-card {
    width: 100%;
    gap: 20px;
    height: 100px;
  }

  .guarantees-card-title {
    max-width: unset;
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    width: 70%;
  }

  .guarantees-card-image img {
    width: 44px;
    height: 44px;
  }

  .guarantees-agreement {
    flex-direction: column;
  }

  .guarantees-agreement h3 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    padding-bottom: 20px;
  }

  .guarantees-download a {
    color: var(--primary-bg-color);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
  }

  .advantages-block-cards {
    flex-direction: column;
  }

  .guarantees-section {
    background: url("../../Images/guaranteesBgMobile.png");
    background-size: cover;
  }

  .advantages-block-title {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 800;
    line-height: 36px;
    text-align: left;
  }

  .guarantees-title {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 800;
    line-height: 36px;
    text-align: left;
  }
}
