.reviews__section {
  background: #f4f6f8;
  padding-top: 100px;
  padding-bottom: 100px;
}

.reviews__section_header {
  text-align: center;
}

.reviews__section_header h1 {
  color: var(--hover-text-color);
  text-align: center;
  font-size: 44px;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: 54px;
}

/* slider */

.reviews__section_videos {
  position: relative;
  margin-top: 2rem;
}

.mySwiper2 {
  width: 100%;
  height: 100%;
}

.mySwiper2 .swiper-slide {
  background: #f4f6f8;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.mySwiper2 .swiper-slide-active {
  /* scale 1.2*/
  transform: scale(1.2);
}

.swiper-button-next,
.swiper-button-prev {
  background: var(--primary-bg-color);
  color: var(--primary-text-color) !important;
  padding: 15px 20px;
  border-radius: 35px;
  font-size: 20px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
}

.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
  font-size: 25px !important;
}

.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after {
  font-size: 25px !important;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 4px);
  border-radius: 0;
  width: 130px;
  background: var(--primary-bg-color);
  height: 4px;
}

.swiperLeft2,
.swiperRight2 {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  border-radius: 35px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 17vw;
  z-index: 999;
  top: 7vw;
  transition: 0.3s ease-in-out;
}

.swiperLeft2:hover,
.swiperRight2:hover {
  background: #f4f6f8;
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.swiperLeft2 {
  left: -3vw;
}

.swiperRight2 {
  right: -3vw;
}

.swiperPagination2 {
  position: absolute;
  right: 50%;
  bottom: -40px !important;
  text-align: center;
}

.reviews__section_videos_mobile {
  display: none;
}

@media screen and (max-width: 1150px) {
  .reviews__section_videos_mobile {
    display: unset;
  }

  .reviews__section_videos {
    display: none;
  }

  .reviews__section_header h1 {
    color: var(--hover-text-color);
    text-align: center;
    font-size: 28px;
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    padding-bottom: 20px;
  }

  .mySwiper3 img {
    width: 100%;
  }

  .mySwiper3 .swiper-slide {
    text-align: left;
  }

  .mySwiper3 .swiper-slide h1 {
    color: var(--hover-text-color);
    font-size: 20px;
    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
  }

  .mySwiper3 .swiper-slide h1 .bold {
    font-weight: 700;
  }
  .mySwiper3 .swiper-slide h1 .price {
    font-weight: 700;
    color: var(--primary-bg-color);
  }

  .reviews__section {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .swiperPagination3 {
    position: absolute;
    right: 50%;
    bottom: -40px !important;
    text-align: center;
  }
}
