header {
  padding-top: 40px;
}

.nav__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__links {
  display: flex;
  align-items: center;
  gap: 40px;
}

.nav__menu ul li {
  list-style: none;
}

.nav__links li a {
  text-decoration: none;
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.nav__menu ul li a:hover {
  color: var(--hover-link-color);
  transition: 0.3s ease-in-out;
}

.overlay__open {
  display: none;
}

.nav__numbers {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav__numbers a {
  text-decoration: none;
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: var(--hover-text-color);
}

.nav__buttons {
  display: flex;
  gap: 8px;
}

.nav__buttons a {
  color: #ffffff;
  background: var(--hover-text-color);
  border-radius: 30px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.nav__buttons_mobile a {
  color: #ffffff;
  background: var(--hover-text-color);
  border-radius: 30px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.nav__buttons_mobile {
  display: none;
}

@media screen and (max-width: 1120px) {
  .nav__buttons_mobile {
    display: flex;
    gap: 12px;
  }

  .overlay__open {
    display: unset;
  }
  .nav__links {
    display: none;
  }

  .nav__links-smallscreen-menu {
    display: unset;
  }

  .nav__links-smallscreen_overlay {
    display: flex;
  }

  .nav__links-smallscreen li {
    list-style: none;
  }

  .nav__links-smallscreen a {
    text-decoration: none;
    color: var(--hover-text-color);
  }

  .nav__links-smallscreen a:hover {
    text-decoration: none;
    color: var(--hover-link-color);
  }

  .nav__links-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2rem;
    text-align: center;
    z-index: 999;
  }

  .nav__links-smallscreen {
    margin-top: 60px;
  }

  .nav__links-smallscreen li {
    margin-top: 2rem;
  }

  .overlay__close,
  .overlay__open {
    font-size: 27px;
    cursor: pointer;
    position: absolute;
    top: 70px;
    right: 20px;
  }

  .nav__buttons {
    display: none;
  }

  .nav__menu {
    padding: 1rem;
    display: flex;
    justify-content: unset;
    gap: 45px;
  }

  .nav__logo a img {
    width: 48px;
    height: 48px;
  }

  .nav__numbers {
    display: none;
  }

  .nav__links-smallscreen a {
    font-family: Inter;
    font-size: 24px;
    font-weight: 500;
    line-height: 29px;
    letter-spacing: 0.02em;
    text-align: center;
  }

  .nav__numbers_mobile {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-top: 60px;
  }

  .nav__numbers_mobile svg {
    color: var(--hover-link-color);
  }

  .nav__numbers_mobile a {
    font-family: Inter;
    font-size: 24px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: center;
  }

  .nav__buttons_overlay_mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-top: 22px;
  }

  .nav__buttons_overlay_mobile a {
    color: #ffffff;
    background: var(--primary-bg-color);
    border-radius: 30px;
    padding: 8px;
    display: flex;
    align-items: center;
  }

  .nav__politics a {
    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: center;
    color: grey;
    border-bottom: 1px solid grey;
  }
}
