.price-comparison-section {
  padding-top: 100px;
  background: var(--hover-bg-color);
  padding-bottom: 100px;
}

.price-comparison-section .container {
  position: relative;
}

.price-comparison-header {
  padding-bottom: 100px;
}

.price-comparison-header h1 {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 800;
  line-height: 54px;
}

.price-comparison-header h3 {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

.price-comparison-heading {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 26px;
  font-style: normal;
  font-weight: 800;
  line-height: 54px; /* 207.692% */
}

.price-comparison-block {
  background: var(--primary-text-color);
  padding-top: 50px;
}

.price-comparison-block-wrapper {
  display: flex;
  align-items: center;
  gap: 30px;
  justify-content: center;
}

.price-comparison-btn {
  padding: 15px 40px;
  background: var(--primary-bg-color);
  border-radius: 10px;
  color: var(--primary-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px; /* 140% */
  text-decoration: none;
  transition: 0.3s ease-in-out;
}

.price-comparison-btn-block {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.price-comparison-block .auto__slider__section_card a {
  display: none;
}

.price-comparison-btn:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color) 000;
}

.mySwiper4 {
  width: 100%;
  height: 100%;
}

.swiperLeft4,
.swiperRight4 {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  border-radius: 35px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;

  z-index: 999;
  top: 25vw;
}

.swiperLeft4:hover,
.swiperRight4:hover {
  background: var(--hover-bg-color);
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.swiperLeft4 {
  left: 0vw;
}

.swiperRight4 {
  right: 0vw;
}

.comparison-preferences-block {
  display: flex;
  padding-top: 90px;
  gap: 24px;
}

.comparison-preferences-block-left,
.comparison-preferences-block-right {
  padding: 60px 52px;
  width: 50%;
  border-radius: 20px;
  background: var(--primary-text-color);
  box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.08);
}

.comparison-preferences-block-left h1,
.comparison-preferences-block-right h1 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px; /* 122.727% */
}

.comparison-preferences-block-left h3,
.comparison-preferences-block-right h3 {
  color: rgba(0, 0, 0, 0.8);
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px; /* 133.333% */
  padding-top: 16px;
  padding-bottom: 36px;
}

.comparison-preferences-block-left-cards {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.comparison-preferences-block-left-cards-item h4 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 140% */
}

.comparison-preferences-block-left-cards-item {
  display: flex;
  align-items: center;
  gap: 16px;
}

.comparison-preferences-block-right h2 {
  color: var(--hover-link-color);
  font-family: Inter;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 56px; /* 116.667% */
}

.comparison-preferences-block-right h4 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 800;
  line-height: 28px; /* 116.667% */
}

.comparison-preferences-btn {
  padding: 15px 40px;
  background: var(--primary-bg-color);
  border-radius: 10px;
  color: var(--primary-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px; /* 140% */
  text-decoration: none;
  transition: 0.3s ease-in-out;
}

.comparison-preferences-btn:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color);
}

.comparison-preferences-btn-block {
  padding-top: 30px;
}

@media screen and (max-width: 1150px) {
  .price-comparison-block-wrapper {
    flex-direction: column;
  }

  .auto__slider__section_card_specs_item img {
    width: 100px;
  }

  .price-comparison-section .swiper-buttons {
    display: none;
  }

  .comparison-preferences-block {
    flex-direction: column;
    justify-content: center;
  }

  .comparison-preferences-block-left,
  .comparison-preferences-block-right {
    width: 100%;
    padding: 32px 24px;
  }

  .comparison-preferences-block-left h1,
  .comparison-preferences-block-right h1 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px; /* 128.571% */
  }

  .comparison-preferences-block-left h3,
  .comparison-preferences-block-right h3 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px; /* 155.556% */
  }

  .comparison-preferences-block-right h4 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 800;
    line-height: 28px; /* 140% */
  }
  .comparison-preferences-btn {
    display: block;
    text-align: center;
  }
}
