.form-section {
  background: url("../../Images/formBg.png");
  padding-top: 120px;
  padding-bottom: 100px;
  background-size: cover;
  background-repeat: no-repeat;
}

.form-section .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 70px;
}

.form-section-left-block {
  width: 40%;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background: var(--primary-text-color);
  padding: 40px 32px;
  height: 500px;
}

.form-section-left-block h3 {
  color: #000;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px; /* 133.333% */
}

.form-section-left-block h4 {
  color: rgba(0, 0, 0, 0.8);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  padding-top: 12px;
  padding-bottom: 20px;
}

.form-section-left-block input {
  border-radius: 8px;
  border: 1px solid #000;
  background: var(--primary-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 18px 20px;
  width: 100%;
}

.form-section-left-block input::placeholder {
  color: rgba(0, 0, 0, 0.2);
}

.form-btn {
  background: var(--primary-bg-color);
  border-radius: 8px;
  border: none;
  color: var(--primary-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 18px 20px;
  margin-top: 16px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  width: 100%;
}

.form-btn:hover {
  background: #f4f6f8;
  color: #000;
  transition: 0.3s ease-in-out;
}

.form-checkbox {
  padding-top: 8px;
  display: flex;
}

.form-checkbox input {
  width: 20px;
}

.form-checkbox label {
  margin-left: 8px;
}

.form-section-left-block-contact h4 {
  color: #000;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 120% */
}

.form-section-left-block-contact-icon {
  display: flex;
  align-items: center;
  text-decoration: none;
  border: 1px solid var(--primary-border-color);
  border-radius: 50px;
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  padding: 10px 16px;
  gap: 8px;
  transition: 0.3s ease-in-out;
}

.form-section-left-block-contact-icon:hover {
  background: var(--primary-border-color);
  color: var(--primary-text-color);
  transition: 0.3s ease-in-out;
}

.form-section-left-block-contact-icons {
  display: flex;
  gap: 12px;
}

.form-section-left-block-contact-icon svg {
  color: var(--primary-text-color);
  background: #000000;
  border-radius: 30px;
  padding: 5px;
  width: 25px;
  height: 25px;
}

.form-section-right-block {
  width: 60%;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  background: var(--primary-text-color);
  padding: 40px 32px;
  height: 500px;
}

.form-section-right-block h1 {
  color: #000;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px; /* 122.727% */
}

.form-section-right-block h4 {
  color: #000;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px; /* 140% */
}

.form-section-right-block-check {
  display: flex;
  gap: 15px;
  align-items: center;
  margin-top: 24px;
}

@media screen and (max-width: 1150px) {
  .form-section {
    background: url("../../Images/formBgMobile.png");
    padding-top: 64px;
    padding-bottom: 64px;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .form-section .container {
    flex-direction: column-reverse;
    gap: 20px;
  }

  .form-section-left-block,
  .form-section-right-block {
    width: 100%;
    height: unset;
  }

  .form-section-right-block h1 {
    color: #000;
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px; /* 128.571% */
  }

  .form-section-right-block h4 {
    color: #000;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px; /* 150% */
  }

  .form-btn {
    padding: 18px 10px;
  }
}
