.question-section {
  padding-top: 100px;
  padding-bottom: 100px;
}

.question-section-header h1 {
  color: #000;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px; /* 122.727% */
}

.question-section .step_number {
  display: none;
}

.buying__process__section_step {
  padding-left: 0;
  padding-right: 0;
}

.question-section .text-block-heading {
  display: none;
}

.question-section .text-block-text {
  max-width: unset;
}

.question-section .line {
  height: 1px;
  background: rgba(0, 0, 0, 0.3);
  width: 100%;
}

.question-section .step_text {
  color: #000;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px; /* 133.333% */
}

.question-section .buying__process__section_step_text2 {
  padding-left: 0;
}
