.help__section {
  background: var(--hover-bg-color);
  padding-top: 120px;
  padding-bottom: 120px;
}

.help__section_header {
  text-align: center;
}

.help__section_header h1 {
  font-family: Inter;
  font-size: 44px;
  font-weight: 800;
  line-height: 54px;
  letter-spacing: 0em;
  text-align: center;
  padding-bottom: 24px;
}

.help__section_header h4 {
  font-family: Inter;
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 0em;
  text-align: center;
  padding-bottom: 28px;
}

.help__section_header a {
  font-family: Inter;
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  background: var(--primary-bg-color);
  padding: 14px 40px;
  text-decoration: none;
  color: var(--primary-text-color);
  border-radius: 8px;
  transition: 0.3s ease-in-out;
}

.help__section_header a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: #000000;
}

.help__section_cards {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.help__section_card h1 {
  font-family: Inter;
  font-size: 36px;
  font-weight: 700;
  line-height: 44px;
  letter-spacing: 0em;
}

.help__section_card h4 {
  font-family: Inter;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0em;
  color: #000000cc;
  margin-top: 27px;
}

.help__section_card {
  box-shadow: 0px 2px 16px 0px #00000014;
  border-radius: 30px;
  padding: 24px 32px;
  width: 411px;
  height: 264px;
  position: relative;
  background: var(--primary-text-color);
}

.help__section_card img {
  position: absolute;
  bottom: 10px;
  right: 30px;
}

.help__section_card_imageMobile {
  display: none;
}

.help__section_mobileBtn {
  display: none;
}

@media screen and (max-width: 1300px) {
  .help__section_card {
    width: 75%;
    margin: 0 auto;
    height: 350px;
  }
}

@media screen and (max-width: 1150px) {
  .help__section_cards {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .help__section_card {
    width: 100%;
    height: 250px;
    padding: 20px;
  }

  .help__section_card_header {
    display: flex;
    align-items: center;
    gap: 48px;
  }

  .help__section_card img {
    display: none;
  }

  .help__section_card_imageMobile {
    display: block;
  }

  .help__section_card_imageMobile img {
    display: unset;
    position: unset;
    width: 100%;
  }

  .help__section_card h1 {
    font-family: Inter;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: left;
  }

  .help__section_card h4 {
    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
  }

  .help__section_header a {
    display: none;
  }

  .help__section_mobileBtn {
    display: block;
    margin-top: 40px;
  }

  .help__section_mobileBtn a {
    font-family: Inter;
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    letter-spacing: 0em;
    background: var(--primary-bg-color);
    padding: 14px 40px;
    text-decoration: none;
    color: var(--primary-text-color);
    border-radius: 8px;
    transition: 0.3s ease-in-out;
    display: block;
    text-align: center;
  }
}
