.advice__section {
  padding-top: 80px;
  padding-bottom: 80px;
}

.advice__section .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}

.advice__section_content h1 {
  color: var(--hover-text-color);
  font-size: 44px;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: 54px;
  padding-bottom: 20px;
}

.advice__section_content h1 span {
  color: var(--primary-bg-color);
}

.advice__section_content h4 {
  color: rgba(0, 0, 0, 0.8);
  font-size: 20px;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  padding-bottom: 32px;
}

.advice__section_content a {
  font-family: Inter;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  background: var(--primary-bg-color);
  padding: 18px 55px;
  text-decoration: none;
  color: var(--primary-text-color);
  border-radius: 8px;
  transition: 0.3s ease-in-out;
}

.advice__section_content a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color) 000;
}

@media screen and (max-width: 1150px) {
  .advice__section .container {
    flex-direction: column-reverse;
  }

  .advice__section_image img {
    width: 100%;
  }

  .advice__section_content a {
    text-align: center;
    display: block;
  }

  .advice__section_content h1 {
    color: var(--hover-text-color);
    font-size: 28px;
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
  }

  .advice__section_content h4 {
    color: var(--hover-text-color);
    font-size: 16px;
    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }

  .advice__section_content a {
    color: var(--primary-text-color);
    text-align: center;
    font-size: 20px;
    font-family: Inter;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
  }
}
