.hero__section__content h1 {
  font-family: Inter;
  font-size: 52px;
  font-weight: 900;
  line-height: 62px;
  letter-spacing: 0em;
  padding-bottom: 20px;
}

.hero__section__content h4 {
  font-family: Inter;
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: 0em;
  padding-bottom: 44px;
}

.hero__section__content a {
  font-family: Inter;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  background: var(--primary-bg-color);
  padding: 18px 55px;
  text-decoration: none;
  color: var(--primary-text-color);
  border-radius: 8px;
  transition: 0.3s ease-in-out;
}

.hero__section__content a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color);
}

.hero__section .container {
  display: flex;
  justify-content: space-between;
  margin-top: 120px;
  padding-bottom: 40px;
  align-items: center;
}

.hero__section__image__mobile {
  display: none;
}

@media screen and (max-width: 1300px) {
  .hero__section__image img {
    width: 100%;
  }
}

@media screen and (max-width: 1100px) {
  .hero__section .container {
    flex-direction: column;
    margin-top: 40px;
  }

  .hero__section__content h1 {
    font-family: Inter;
    font-size: 32px;
    font-weight: 900;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: center;
  }

  .hero__section__content h4 {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;
  }

  .hero__section__content a {
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;
    display: block;
  }

  .hero__section__image {
    display: none;
  }

  .hero__section__image__mobile {
    display: block;
  }

  .hero__section__image__mobile img {
    width: 100%;
  }

  .hero__section__content {
    text-align: center;
  }
}
