.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: rgba(0, 0, 0, 0.5); */
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: none;
}

.modal-window.active {
  display: flex;
}

.modal-window-content {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  width: 100%;
  max-width: 500px;
  max-height: 500px;
  overflow: auto;
  text-align: center;
}

.modal-window-content p {
  padding-top: 20px;
  padding-bottom: 20px;
}

.modal-window-close {
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.modal-window-close:hover {
  color: var(--primary-bg-color);
  transition: 0.3s ease-in-out;
}

.modal-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  border-radius: 10px;
  background: var(--primary-bg-color);
  color: #fff;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  text-decoration: none;
  max-width: 300px;
  margin: 0 auto;
}

.modal-btn:hover {
  background: #f4f6f8;
  transition: 0.3s ease-in-out;
  color: #000000;
}
