.auto__slider__section {
  background: var(--hover-bg-color);
  padding-top: 100px;
  padding-bottom: 120px;
}

.auto__slider__section_cards {
  padding-top: 40px;
}

.auto__slider__section_card {
  background: #fff;
  box-shadow: 0px 2px 16px 0px #00000014;
  border-radius: 20px;
  max-width: 420px;
  padding: 24px 20px;
}

.auto__slider__section_card img {
  display: block;
  margin: 0 auto;
}

.auto__slider__section_header h1 {
  font-family: Inter;
  font-size: 44px;
  font-weight: 800;
  line-height: 54px;
  letter-spacing: 0em;
  padding-bottom: 20px;
}

.auto__slider__section_header h4 {
  font-family: Inter;
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0em;
}

.auto__slider__section_card_specs_item {
  display: flex;
  justify-content: space-between;
}

.auto__slider__section_card_specs_item img {
  object-fit: contain;
  /* Дополнительные стили */
  width: 100px; /* Замените на нужную вам ширину */
  height: 25px; /* Замените на нужную вам высоту */
}

.auto__slider__section_card h3 {
  font-family: Inter;
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: 0em;
  margin-top: 24px;
}

.auto__slider__section_card_specs {
  margin-top: 16px;
}

.auto__slider__section_card_specs_item p {
  color: rgba(0, 0, 0, 0.5);
  font-size: 16px;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.auto__slider__section_card_specs_item h4 {
  color: #000;
  text-align: right;
  font-size: 20px;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.auto__slider__section_card_prices {
  margin-top: 28px;
  padding-bottom: 24px;
}

.auto__slider__section_card_prices_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
}

.auto__slider__section_card_prices_item h6 {
  color: var(--hover-text-color);
  font-size: 16px;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  max-width: 200px;
}

.auto__slider__section_card_prices_item h4 span {
  color: var(--hover-link-color);
  font-size: 24px;
  font-family: Inter;
  font-style: normal;
  font-weight: 900;
  line-height: 24px;
}

.auto__slider__section_card_prices_item h4 {
  color: var(--hover-text-color);
  text-align: right;
  font-size: 24px;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.auto__slider__section_card a {
  font-family: Inter;
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  background: var(--primary-bg-color);
  padding: 14px 40px;
  text-decoration: none;
  color: var(--primary-text-color);
  border-radius: 8px;
  transition: 0.3s ease-in-out;
  display: block;
  text-align: center;
}

.auto__slider__section_card a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color);
}

.auto__slider__section_cards_mobile {
  display: none;
}

.swiperLeft,
.swiperRight {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  border-radius: 35px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 17vw;
  z-index: 999;
  transition: 0.3s ease-in-out;
}

.swiperLeft:hover,
.swiperRight:hover {
  background: var(--hover-bg-color);
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.swiperLeft {
  left: -3vw;
}

.swiperRight {
  right: -3vw;
}

.swiperPagination {
  position: absolute;
  right: 50%;
  bottom: -40px !important;
  text-align: center;
}

.auto__slider__section_cards {
  position: relative;
}

@media screen and (max-width: 1150px) {
  .auto__slider__section_cards {
    display: none;
  }

  .auto__slider__section_cards_mobile {
    display: unset;
  }

  .auto__slider__section_card img {
    width: 100%;
  }

  .auto__slider__section_card_specs_item img {
    width: 100px;
  }

  .auto__slider__section_header h1 {
    color: #000;
    font-size: 28px;
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
  }

  .auto__slider__section_header h4 {
    color: #000;
    font-size: 16px;
    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }

  .auto__slider__section {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .auto__slider__section_card {
    margin: 0 auto;
  }
}

/* slider */

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-button-next,
.swiper-button-prev {
  background: var(--primary-bg-color);
  color: var(--primary-text-color) !important;
  padding: 15px 20px;
  border-radius: 35px;
  font-size: 20px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
}

.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
  font-size: 25px !important;
}

.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after {
  font-size: 25px !important;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 4px);
  border-radius: 0;
  width: 130px;
  background: var(--primary-bg-color);
  height: 4px;
}
