.stories__section {
  padding-top: 100px;
  padding-bottom: 100px;
  background: var(--hover-bg-color);
}

.stories__section .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 70px;
}

.stories__section_text h1 {
  color: var(--hover-text-color);
  font-size: 44px;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: 54px;
  padding-bottom: 40px;
}

.stories__section_text h1 span {
  color: var(--primary-bg-color);
}

.stories__section_text a {
  color: var(--primary-text-color);
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
  background: var(--primary-bg-color);
  padding: 18px 55px;
  text-decoration: none;
  color: var(--primary-text-color);
  border-radius: 8px;
  transition: 0.3s ease-in-out;
}

.stories__section_text a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color) 000;
}

@media screen and (max-width: 1150px) {
  .stories__section .container {
    flex-direction: column;
    gap: 20px;
  }

  .stories__section_image img {
    width: 100%;
  }

  .stories__section_text h1 {
    color: var(--hover-text-color);
    font-size: 28px;
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    padding-bottom: 20px;
  }

  .stories__section_text a {
    font-size: 20px;
    display: block;
    text-align: center;
  }

  .stories__section {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
