footer {
  padding-top: 100px;
  padding-bottom: 100px;
}

footer .footer-header h1 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px; /* 122.727% */
}

footer .footer-block h3 {
  color: rgba(0, 0, 0, 0.5);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 100% */
}

footer .footer-block h4 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 140% */
  padding-top: 20px;
}

footer .footer-block a {
  text-decoration: none;
}

footer .footer-blocks {
  padding-top: 60px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 40px;
}

.footer-block-socials {
  display: flex;
  gap: 12px;
  padding-top: 20px;
}

.footer-block-social {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 30px;
  transition: 0.3s ease-in-out;
}

.footer-block-social:hover {
  background: var(--hover-bg-color);
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.footer-politics {
  padding-top: 55px;
}

.footer-politics a {
  color: rgba(0, 0, 0, 0.3);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
  text-decoration-line: underline;
  margin-left: 100px;
  transition: 0.3s ease-in-out;
}

.footer-politics a:hover {
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.footer-politics a:first-child {
  margin-left: 0px;
}

@media screen and (max-width: 1150px) {
  footer .footer-blocks {
    display: flex;
    flex-direction: column;
  }

  .footer-politics {
    display: flex;
    flex-direction: column;
  }

  .footer-politics a {
    margin-left: 0;
  }

  footer iframe {
    margin-top: 20px;
  }

  footer .footer-header h1 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px; /* 128.571% */
  }
}
