.buying__process__section {
  padding-top: 100px;
  padding-bottom: 100px;
}

.buying__process__section_header h1 {
  color: var(--hover-text-color);
  font-size: 44px;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: 54px;
  padding-bottom: 40px;
}

.buying__process__section .line {
  height: 1px;
  background: rgba(0, 0, 0, 0.3);
  width: 100%;
}

.buying__process__section_step_text {
  display: flex;
  gap: 120px;
  align-items: center;
}

.buying__process__section_step_text .step_number {
  color: var(--primary-bg-color);
  font-size: 80px;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: 88px;
}

.buying__process__section_step {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 100px;
}

.buying__process__section_step_text .step_text {
  color: var(--hover-text-color);
  font-size: 36px;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  line-height: 44px;
}

.buying__process__section_step_button a {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  padding: 20px 20px;
  border-radius: 35px;
  font-size: 20px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.buying__process__section_step_button a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color);
}

.text-block-heading {
  color: var(--hover-text-color);
  font-size: 20px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.text-block-text {
  color: rgba(0, 0, 0, 0.8);
  font-size: 18px;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  max-width: 500px;
}

.text-block {
  display: flex;
  justify-content: space-between;
}

.buying__process__section_step_text2 {
  display: flex;
  gap: 24px;
  flex-direction: column;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 314px;
}

@media screen and (max-width: 1150px) {
  .buying__process__section_step {
    padding: 28px 16px;
  }

  .buying__process__section_step_text {
    gap: 20px;
  }

  .buying__process__section_step_text .step_text {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
  }

  .buying__process__section_step_text .step_number {
    color: var(--primary-bg-color);
    font-family: Inter;
    font-size: 52px;
    font-style: normal;
    font-weight: 700;
    line-height: 52px;
  }

  .buying__process__section_step_text2 {
    padding: 0;
  }

  .text-block {
    flex-direction: column;
    gap: 16px;
  }

  .buying__process__section_steps {
    padding-bottom: 24px;
  }

  .buying__process__section_header h1 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
  }
}
