.our-team-section {
  padding-top: 100px;
  padding-bottom: 100px;
  background: var(--hover-bg-color);
}

.our-team-section-header h1 {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 800;
  line-height: 54px; /* 122.727% */
}

.our-team-section-header h4 {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px; /* 133.333% */
  margin-top: 28px;
}

.our-team-section-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(1, 1fr);
  grid-column-gap: 24px;
  grid-row-gap: 24px;
  padding-top: 40px;
}

.our-team-section-card {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 32px;
  padding-top: 300px;
  border-radius: 20px;
  width: 411px;
  height: 496px;
}

.our-team-section-card-name {
  color: #fff;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px; /* 133.333% */
  padding-bottom: 8px;
}

.our-team-section-card-position {
  color: #fff;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 120% */
  padding-bottom: 24px;
}

.our-team-section-card-number {
  color: #fff;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding-bottom: 16px;
}

.our-team-section-card-socials {
  display: flex;
  gap: 8px;
}

.our-team-section-card-socials a {
  color: var(--primary-text-color);
  background: var(--hover-text-color);
  border-radius: 30px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.our-team-section-card-text-mobile {
  display: none;
}

.our-team-reviews-header h1 {
  color: var(--hover-text-color);
  text-align: center;
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px; /* 122.727% */
}

.our-team-reviews {
  padding-top: 100px;
}

.mySwiper7 {
  width: 100%;
  height: 100%;
}

.mySwiper7 .swiper-slide {
  background: var(--hover-bg-color);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.mySwiper7 .swiper-slide-active {
  /* scale 1.2*/
  transform: scale(1.2);
}

.swiperLeft7,
.swiperRight7 {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  border-radius: 35px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 17vw;
  z-index: 999;
  top: 7vw;
}

.swiperLeft7 {
  left: -3vw;
}

.swiperRight7 {
  right: -3vw;
}

.swiperPagination7 {
  position: absolute;
  right: 50%;
  bottom: -40px !important;
  text-align: center;
}

.our-team-reviews-slider {
  position: relative;
  padding-top: 40px;
}

.our-team-reviews-slider-mobile {
  display: none;
}

@media screen and (max-width: 1300px) {
  .our-team-section-card {
    width: 100%;
  }
}

@media screen and (max-width: 1150px) {
  .our-team-section-card-number {
    color: #000;
  }
  .our-team-section-card-name {
    color: #000;
  }
  .our-team-section-card-position {
    color: #000;
  }
  .our-team-reviews-slider {
    display: none;
  }

  .our-team-reviews-slider-mobile {
    display: unset;
  }

  .our-team-reviews-slider-mobile .mySwiper8 .swiper-slide {
    background: unset;
  }

  .our-team-reviews-header h1 {
    color: var(--hover-text-color);
    text-align: center;
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px; /* 128.571% */
    padding-bottom: 20px;
  }
  .our-team-section-card h4,
  .our-team-section-card a {
    display: none;
  }

  .our-team-section-card-text-mobile {
    display: unset;
    text-align: center;
  }

  .our-team-section-cards {
    display: flex;
    flex-direction: column;
  }

  .our-team-section-card {
    width: 100%;
  }

  .our-team-section-card-socials {
    justify-content: center;
  }

  .our-team-section-header h1 {
    color: var(--hover-text-color);
    text-align: center;
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px; /* 128.571% */
  }

  .our-team-section-header h4 {
    color: var(--hover-text-color);
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
  }
  .our-team-section {
    padding-top: 64px;
    padding-bottom: 64px;
  }
}
