.about__section {
  display: flex;
  align-items: center;
}

.about__section_content {
  width: 50%;
  padding: 100px 50px;
}

.about__section_image {
  width: 50%;
}

.about__section_image img {
  width: 100%;
}

.about__section_content h1 {
  font-family: Inter;
  font-size: 44px;
  font-weight: 900;
  line-height: 54px;
  letter-spacing: 0em;
  padding-bottom: 12px;
}

.about__section_content h4 {
  font-family: Inter;
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 0em;
  padding-bottom: 24px;
}

.about__section_card {
  display: flex;
  gap: 32px;
  align-items: center;
}

.about__section_card h5 {
  font-family: Inter;
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: 0em;
}

.about__section_cards {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.about__section_additional {
  border: 3px solid var(--transparent-border-color);
  border-radius: 10px;
  padding: 12px 20px;
  max-width: 500px;
  margin-top: 24px;
}

.about__section_additional p {
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
}

.about__section_content a {
  font-family: Inter;
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  background: var(--primary-bg-color);
  padding: 18px 55px;
  text-decoration: none;
  color: var(--primary-text-color);
  border-radius: 8px;
  transition: 0.3s ease-in-out;
}

.about__section_content a:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color);
}

.about__section_button {
  margin-top: 32px;
}

@media screen and (max-width: 1150px) {
  .about__section {
    flex-direction: column;
  }

  .about__section_image {
    width: 100%;
  }

  .about__section_image img {
    width: 100%;
  }

  .about__section_content {
    width: 100%;
    padding: 16px 32px;
  }

  .about__section_content h1 {
    font-family: Inter;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
  }

  .about__section_content h4 {
    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
  }

  .about__section_card {
    gap: 20px;
  }

  .about__section_card h5 {
    font-family: Inter;
    font-size: 16px;
    font-weight: 500;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;
  }

  .about__section_card img {
    width: 44px;
    height: 44px;
  }

  .about__section_additional {
    width: 100%;
  }

  .about__section_button a {
    display: block;
    text-align: center;
  }
}
