.economy-section {
  padding-top: 100px;
  padding-bottom: 100px;
}

.economy-section-header-left h1 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px; /* 122.727% */
  padding-bottom: 20px;
}

.economy-section-header-left h4 {
  color: rgba(0, 0, 0, 0.8);
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px; /* 133.333% */
  padding-bottom: 32px;
}

.economy-section-header-left .economy-section-btn {
  padding: 15px 40px;
  background: var(--primary-bg-color);
  border-radius: 10px;
  color: var(--primary-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px; /* 140% */
  text-decoration: none;
  transition: 0.3s ease-in-out;
}

.economy-section-header-left .economy-section-btn:hover {
  background: var(--hover-bg-color);
  transition: 0.3s ease-in-out;
  color: var(--hover-text-color);
}

.economy-section-header-right {
  border-radius: 10px;
  border: 3px solid var(--transparent-border-color);
  padding: 30px 16px;
}

.economy-section-header-right h4 {
  color: var(--hover-text-color);
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 40px; /* 200% */
}

.economy-section-header-right h4 span {
  color: var(--primary-bg-color);
  font-family: Inter;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 40px;
}

.economy-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.economy-section-slider {
  position: relative;
  padding-top: 45px;
}

.swiperLeft5,
.swiperRight5 {
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
  border-radius: 35px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 999;
  top: 9vw;
  transition: 0.3s ease-in-out;
}

.swiperLeft5:hover,
.swiperRight5:hover {
  background: var(--hover-bg-color);
  color: var(--hover-text-color);
  transition: 0.3s ease-in-out;
}

.swiperLeft5 {
  left: -3vw;
}

.swiperRight5 {
  right: -3vw;
}

.economy-section-slider-mobile {
  display: none;
}

.economy-section-btn-mobile {
  display: none;
}

@media screen and (max-width: 1150px) {
  .economy-section {
    padding-top: 60px;
  }
  .economy-section-slider {
    display: none;
  }

  .economy-section-header {
    flex-direction: column;
    gap: 20px;
  }

  .economy-section-header h1 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px; /* 128.571% */
  }

  .economy-section-header h4 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
    padding-bottom: 0;
  }

  .economy-section-header-right h4 {
    color: var(--hover-text-color);
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px; /* 155.556% */
  }

  .economy-section-header-right h4 span {
    color: var(--primary-bg-color);
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 800;
    line-height: 40px;
    display: block;
  }

  .economy-section-header-left .economy-section-btn {
    display: none;
  }

  .economy-section-slider-mobile {
    display: block;
    padding-top: 32px;
    position: relative;
  }

  .mySwiper6 {
    width: 100%;
    height: 100%;
  }

  .mySwiper6 .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: var(--primary-text-color);

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .mySwiper6 .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .mySwiper6
    .swiper-horizontal
    > .swiper-pagination-bullets
    .swiper-pagination-bullet,
  .swiper-pagination-horizontal.swiper-pagination-bullets
    .swiper-pagination-bullet {
    width: 70px;
  }

  .economy-section-btn-mobile {
    display: block;
    padding: 15px 40px;
    background: var(--primary-bg-color);
    border-radius: 10px;
    color: var(--primary-text-color);
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px; /* 140% */
    text-decoration: none;
    transition: 0.3s ease-in-out;
    margin: 0 auto;
    margin-top: 45px;
    text-align: center;
  }

  .economy-section-btn-mobile:hover {
    background: var(--hover-bg-color);
    transition: 0.3s ease-in-out;
    color: var(--hover-text-color);
  }
}
